@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-container {
  position: relative;
  text-align: center;
  border: 2px solid mediumturquoise;
  width: 400px;
  height: 640px;
  border-radius: 5px;
  padding: 20px;
}


.header-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;

  padding: 10px 0;
}

.header-container h3 {
  margin: 0;
  font-family: "Muli", sans-serif;
  letter-spacing: 1px;
  font-weight: bold;
}

.btn-add {
  padding: 10px;
  color: white;
  background-color: green;
  border: none;
  border-radius: 3px;
  font-family: inherit;
  font-size: 18px;
  width: 100px;
}

.btn-add:hover {
  opacity: 80%;
}

.btn-add:focus {
  outline: none;
}

.btn-add:active {
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.tasks {
  width: 100%;
  margin-top: 20px;
  overflow-y: scroll;
}

.tasks::-webkit-scrollbar {
  display: none;
}

.task {
  position: relative;
  background-color: ghostwhite;
  border-radius: 2px;
  margin-bottom: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 10px;
  cursor: pointer;
}

.task h6 {
  margin: 0;
  letter-spacing: 1px;
}

.task p {
  margin: 0;
}

.cross {
  position: absolute;
  right: 10px;
  top: 10px;
  color: red;
  cursor: pointer;
}

.cross:hover {
  color: darkred;
}

.cross:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.app-footer {
  position: absolute;
  bottom: 10px;
  left: 50px;
}


.form-group {
  text-align: left;
}

.form-check {
  text-align: left;
}

